
.header-column-container[data-v-3530fdca]{position:fixed;width:100vw;height:100vh
}
.header-column[data-v-3530fdca]{position:fixed;top:0;left:calc((100vw - 960px) / 2);height:100vh;width:960px / 3;z-index:120
}
.body-column[data-v-3530fdca]{position:relative;margin-left:calc(((100vw - 960px) / 2) + (960px / 3));height:100vh;width:640px;z-index:100
}
.credit[data-v-3530fdca]{border-top:1px solid #bbb;padding:0.5rem 0;font-size:0.6rem;color:#bbb
}
.credit a[data-v-3530fdca]{text-decoration:underline
}
@media screen and (max-width: 960px){
.header-column[data-v-3530fdca]{left:0;width:calc(100vw / 3)
}
.body-column[data-v-3530fdca]{position:relative;margin-left:calc(100vw / 3);height:100vh;width:calc(100vw * 2 / 3)
}
}
@media screen and (max-width: 750px){
.header-column[data-v-3530fdca]{position:relative;left:0;height:auto;width:100%
}
.body-column[data-v-3530fdca]{position:relative;margin-left:0;height:auto;width:100%
}
}



